
.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
}

.movies .movie {
  width: 45%;
  height: 230px;
  background-color: #ffffff;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  width: 150px;
  max-width: 120px;
}

.movie__data {
  margin-left: 16px;
  overflow: scroll;
}

.movie__title {
  font-size: 20px;
  color: #2b292d;
  margin-bottom: 8px;
}

.movie__year {
  margin-bottom: 4px;
  font-weight: 300;
}

.genres {
  font-size: 14px;
  color: #666666;
  display: flex;
  margin-bottom: 10px;
  font-weight: 400;
}

.genres li {
  margin-right: 10px;
}

.movie__summary {
  font-size: 14px;
  color: #333333;
  font-weight: 400;
}


/* Media-query */
@media screen and (min-width: 512px) and (max-width: 1024px) {
  .movies .movie {
    width: 100%;
    justify-content: flex-start;
  }
}